import React, { useState, useEffect } from 'react'

import { Modal, Button, Table, Radio, Input } from 'antd'

function Feedback() {

    const [visible, setVisible] = useState(false)
    const [confirmLoading, setConfirmLoading] = useState(false)
    const [values, setValues] = useState()

    const handleSubmit = () => {
        setConfirmLoading(true);
        setTimeout(() => {
            setVisible(false)
            setConfirmLoading(false)
        }, 1000);
    };

    const handleCancel = () => {
        console.log('Clicked cancel button');
        setVisible(false)
    };

    const handleClick = (key) => {
        setVisible(true)
    }

    const onChange = e => {
        console.log('radio checked', e.target.value);
        setValues(e.target.value)
    };

    const columns = [
        { title: 'Event Name', dataIndex: 'name', key: 'name' },
        { title: 'Event Date', dataIndex: 'date', key: 'date' },
        {
            title: 'Action',
            dataIndex: '',
            key: 'x',
            render: (text, record) =>
                data.length >= 1 ? (
                    // <Popconfirm title="Sure to Unregister?" onConfirm={() => this.handleDelete(record.key)}>
                    <a onClick={() => handleClick(record.key)} >Take Survey</a>
                    // </Popconfirm>
                ) : null,
        },
    ];

    const radioStyle = {
        display: 'block',
        height: '30px',
        lineHeight: '30px',
    };

    const data = [
        {
            key: 1,
            name: 'John Brown',
            date: 32,
            address: 'New York No. 1 Lake Park',
        },
        {
            key: 2,
            name: 'Jim Green',
            date: 42,
            address: 'London No. 1 Lake Park',
        },
        {
            key: 3,
            name: 'Not Expandable',
            date: 29,
            address: 'Jiangsu No. 1 Lake Park',
        },
        {
            key: 4,
            name: 'Joe Black',
            date: 32,
            address: 'Sidney No. 1 Lake Park',
        },
    ];

    const [surveyData, setSurveyData] = useState([
        {
            key: 1,
            question: "How was the event",
            options: ["It was good", "It was not so good", "It could've been better", "It was the best event ever!"]
        },
        {
            key: 2,
            question: "How was the event",
            options: ["It was good", "It was not so good", "It could've been better", "It was the best event ever!"]
        },
    ]);

    return (
        <div>
            <Modal
                title={<h5 className="h4Tag">Feedback for Workshop on Python</h5>}
                visible={visible}
                onOk={handleSubmit}
                confirmLoading={confirmLoading}
                onCancel={handleCancel}
                footer={[
                    <Button key="back" onClick={handleCancel}>
                        Close
                    </Button>,
                    <Button key="submit" type="primary" loading={confirmLoading} onClick={handleSubmit}>
                        Submit
                    </Button>,
                ]}
            >
                <div>
                    {surveyData.map((data) => (
                        <>
                            <h6>{data.question}</h6>
                            <Radio.Group onChange={onChange} value={values}>
                                {data.options.map((option) => (
                                    <Radio style={radioStyle} value={option}>
                                        {option}
                                    </Radio>
                                ))}
                            <br />
                            </Radio.Group>
                        </>
                    ))}
                </div>
            </Modal>
            <Table bordered columns={columns} dataSource={data} />
        </div>
    )
}



export default Feedback
