import React, { useState } from 'react'
import './EventsUI.css'
import Tabs from './Tabs'
import '../../Css/simple-grid.css'
import Registered from './Registered'
import Feedback from './Feedback'
import Upcoming from './Upcoming'

import { withRouter } from 'react-router-dom'
import { Modal, Menu, Dropdown, Button } from 'antd'
import { UserOutlined } from '@ant-design/icons';
import Profile from './Profile'

function EventsUI(props) {

    const handleLogout = () => {

    }

    const [visible, setVisible] = useState(false)
    const [confirmLoading, setConfirmLoading] = useState(false)

    const showModal = () => {
        setVisible(true)
    };

    const handleSave = () => {
        setConfirmLoading(true);
        setTimeout(() => {
            setVisible(false)
            setConfirmLoading(false)
        }, 1000);
    };

    const handleCancel = () => {
        console.log('Clicked cancel button');
        setVisible(false)
    };

    return (
        <div>
            <Modal
                style={{ top: 30 }}
                title={<h5 className="h4Tag">My Profile</h5>}
                visible={visible}
                onOk={handleSave}
                confirmLoading={confirmLoading}
                onCancel={handleCancel}
                footer={[
                    <Button key="back" onClick={handleCancel}>
                        Close
                    </Button>,
                    <Button key="submit" type="primary" loading={confirmLoading} onClick={handleSave}>
                        Save
                    </Button>,
                ]}
            >
                <Profile />
            </Modal>
            <header>
                <div className="texts">
                    <div className="admin">
                        <div className="dropdown">
                            <button className="dropbtn">Gopal Krishna  <UserOutlined /></button>
                            <div className="dropdown-content">
                                <a onClick={showModal}>My Details</a>
                                <a onClick={handleLogout}>Logout</a>
                            </div>
                        </div>
                    </div>
                    <Tabs>
                        <div label="UPCOMING" className="events" id="first">
                            <br />
                            <br />
                            <Upcoming />
                        </div>

                        <div label="REGISTERED" className="activities" id="acti">
                            <br />
                            <br />
                            <div className="row" style={{ maxWidth: "1200px", marginLeft: "auto", marginRight: "auto", marginTop: "40px", padding: "20px" }}>
                                <Registered />
                            </div>
                        </div>
                        <div label="FEEDBACK" className="survey" id="attendedtable">
                            <br />
                            <br />
                            <div className="row" style={{ maxWidth: "1200px", marginLeft: "auto", marginRight: "auto", marginTop: "40px", padding: "20px" }}>
                                <Feedback />
                            </div>
                        </div>
                        <div label="PROFILE" className="profile" id="attendedtable">
                            <br />
                            <br />
                            <div className="row" style={{ maxWidth: "1200px", marginLeft: "auto", marginRight: "auto", marginTop: "40px", padding: "20px" }}>
                                <h4 style={{ color: "white" }}>This is users profile</h4>
                            </div>
                        </div>
                    </Tabs>
                </div>
            </header>
        </div>
    )
}

export default withRouter(EventsUI)
