import React, { useState, useEffect } from 'react'
import EventDetails from './EventDetails';

import { Modal, Table, Popconfirm, Button } from 'antd'

function Registered() {

    const [visible, setVisible] = useState(false)
    const [confirmLoading, setConfirmLoading] = useState(false)

    const showModal = () => {
        setVisible(true)
    };

    const handleUnregister = () => {
        setConfirmLoading(true);
        setTimeout(() => {
            setVisible(false)
            setConfirmLoading(false)
        }, 1000);
    };

    const handleCancel = () => {
        console.log('Clicked cancel button');
        setVisible(false)
    };

    const columns = [
        { title: 'Event Name', dataIndex: 'name', key: 'name' },
        { title: 'Registered On', dataIndex: 'date', key: 'date' },
        { 
            title: 'Details', 
            dataIndex: 'details', 
            key: 'details',
            render: (text, record) =>
            data.length >= 1 ? (
                <a onClick={showModal} >View Details</a>
            ) : null,
        },
        {
            title: 'Action',
            dataIndex: '',
            key: 'x',
            render: (text, record) =>
            data.length >= 1 ? (
                <Popconfirm title="Sure to Unregister?" onConfirm={() => handleUnregister(record.key)}>
                <a>Unregister</a>
                </Popconfirm>
            ) : null,
        },
    ];

    const data = [
        {
            key: 1,
            name: 'John Brown',
            date: 32,
            address: 'New York No. 1 Lake Park',
        },
        {
            key: 2,
            name: 'Jim Green',
            date: 42,
            address: 'London No. 1 Lake Park',
        },
        {
            key: 3,
            name: 'Not Expandable',
            date: 29,
            address: 'Jiangsu No. 1 Lake Park',
        },
        {
            key: 4,
            name: 'Joe Black',
            date: 32,
            address: 'Sidney No. 1 Lake Park',
        },
    ];

    return (
        <div>
            <Modal
                title={<h5 className="h4Tag">A little bit about the event</h5>}
                visible={visible}
                onOk={handleUnregister}
                confirmLoading={confirmLoading}
                onCancel={handleCancel}
                footer={[
                    <Button key="back" onClick={handleCancel}>
                      Close
                    </Button>,
                    <Button key="submit" type="primary" loading={confirmLoading} onClick={handleUnregister}>
                      Unregister
                    </Button>,
                  ]}
            >
                <EventDetails />
            </Modal>
            <Table bordered columns={columns} dataSource={data} />
        </div>
    )
}



export default Registered
