import React from 'react'

function EventDetails() {
    const event = {
        event_name : "name",
        description : "description",
        organizers: "organizer",
        venue: "venue",
        time: "time",
        date: "date",
        limit: "limit",
        fee: "fee"
    }
    return (
        <div>
            <h5>Event Name : {event.event_name }</h5>
            <h5>Description : { event.description }</h5>
            <h5>Organisers : { event.organizers }</h5>
            <h5>Venue : { event.venue }</h5>
            <h5>Time : { event.time }</h5>
            <h5>Event Date : { event.date }</h5>
            <h5>Limit : { event.limit }</h5>
            <h5>Fee : { event.fee }</h5>
        </div >
    )
}

export default EventDetails
