import React, { useState, useEffect } from 'react'
import { Modal, Button } from 'antd';
import EventDetails from './EventDetails';

function Upcoming(props) {

    const [visible, setVisible] = useState(false)
    const [confirmLoading, setConfirmLoading] = useState(false)

    const showModal = () => {
        setVisible(true)
    };

    const handleRegister = () => {
        setConfirmLoading(true);
        setTimeout(() => {
            setVisible(false)
            setConfirmLoading(false)
        }, 1000);
    };

    const handleCancel = () => {
        console.log('Clicked cancel button');
        setVisible(false)
    };


    return (
        <div>
            <Modal
                title={<h5 className="h4Tag">A little bit about the event</h5>}
                visible={visible}
                onOk={handleRegister}
                confirmLoading={confirmLoading}
                onCancel={handleCancel}
                footer={[
                    <Button key="back" onClick={handleCancel}>
                      Close
                    </Button>,
                    <Button key="submit" type="primary" loading={confirmLoading} onClick={handleRegister}>
                      Register
                    </Button>,
                  ]}
            >
                <EventDetails />
            </Modal>
            <div className="row" style={{ maxWidth: "1200px", marginLeft: "auto", marginRight: "auto", marginTop: "40px", padding: "20px" }}>
                <div className="col col-6-sm">
                    <div className="box-events">
                        <h4 className="title-box">" upcoming.event_name "</h4>
                        <button name="event" className="btn-onimg" value="{{ upcoming.event_id }}" onClick={showModal}>View More</button>
                    </div>
                </div>

                <div className="col col-6-sm">
                    <div className="box-events">
                        <h4 className="title-box">"upcoming.event_name "</h4>
                        <button name="event" className="btn-onimg" value="{{ upcoming.event_id }}" onClick={showModal}>View More</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Upcoming
